@import "../../var";

.top-menu {
  background: $color-main; }

.top-menu > .row {
  color: #fff;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  max-width: 1310px;
  margin: auto; }

.top-menu a {
  color: #fff; }

.user-menu {
  display: flex;
  justify-content: flex-end;
  align-items: stretch;
  height: 100%;
  min-height: 25px;
  padding: 0 10px;
  position: sticky;
  top: 0;
  z-index: 501;

  &:before {
    content: '☰';
    display: block;
    padding: 0 8px;
    align-self: center; }
  &:hover {
    background-color: #3da58f;
    &>span> .nav,
    &>span> .nav a,
    &>.nav,
    &>.nav a {

      display: flex; } } }

.user-menu .icon {
  padding: 5px 16px;
  display: flex; }

.user-menu .icon svg {
  width: 14px;
  height: 14px; }

.user-menu > .row {
  height: 100%;
  flex-basis: 280px;
  justify-content: flex-end;
  align-items: stretch; }

.user-menu > a {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%; }

.user-menu a {
  color: #8d8d8d; }

.user-menu > span.row > a {
  color: #fff;
  display: flex; }

.contacts a {
  color: #fff; }

.contacts {
  color: #fff;
  align-self: center;
  margin-left: 50px; }




.h-upper > div:not([class]) {
  position: relative;
  top: 8px;
  white-space: nowrap; }




.FIO {
  display: flex;
  align-items: center;
  padding: 0 10px;
  font-size: 12px; }

