.user-button {
  flex-direction: column; }

.user-button div:first-child span {

  padding-left: 10px;
  color: #fff; }

.nav {
  display: none;
  flex-flow: column;
  border: 1px solid #eee;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  background: #fff;
  z-index: 2;
  position: absolute;
  top: 100%;
  right: -1px;
  width: 280px; }

.nav a:hover {
  background: #ababab33; }

.nav a {
  padding: 5px 10px; }

.user-menu:hover > span > .nav {
  display: flex; }

/***** top menu */
.top-menu .item {
  padding: 0 10px; }
.icon-menu {
  padding: 3px; }


#icon-locked {
  fill: #e30000; }

